.body {
  height: 100vh;
  margin: 0;
  display: flex;
}

.clock {
  border: 4px dotted #000;
  border-radius: 80vmin;
  width: 80vmin;
  height: 80vmin;
  margin: auto;
  position: relative;
}

.clock__center {
  background-color: #ff0;
  border-radius: 5vmin;
  width: 5%;
  height: 5%;
  position: absolute;
  top: 47.5%;
  left: 47.5%;
}

.clock__hand {
  position: absolute;
  bottom: 50%;
  left: 48.5%;
}

.hand-min {
  transform-origin: bottom;
  background-color: #00f;
  width: 2vmin;
  height: 30%;
  animation-name: move;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

.hand-sec {
  transform-origin: bottom;
  background-color: green;
  width: 1vmin;
  height: 50%;
  animation-name: move;
  animation-duration: 5s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}

@keyframes move {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

